@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "mixins";

.header {
    .btn {
        background: #191919;
        border: 1px solid #FFFFFF;
        box-shadow: 1px 1px 5px #FEC148;
        border-radius: 8px;
    }
}
.container {
    width: 1170px;
    margin: 0 auto;
}
.section {
    margin: 60px 0px 120px 0px;
    h1 {
        margin-left: 150px;
        padding-top: 20px;
        width: 853px;
        text-align: center;
        span {
            color: #FBA82B;
        }
    }
    .section__card {
        padding-top: 40px;
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
        .card {
            width: 370px;
            height: 370px;
            background: #FFFFFF;
            box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.25);
            border-radius: 10px;
            .block__cards {
                padding: 20px;
                img {
                    border-radius: 10px;
                    width: 330px;
                    height: 154px;  
                }
                h2 {
                    padding-top: 20px;
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 38px;
                    line-height: 55px;
                    color: #000000;
                    padding-bottom: 20px;
                }
                .btn {

                }
            }
        }
        .card:nth-child(1) {
            .block__cards {
                h2 {
                    padding-bottom: 76px;
                }
            }
        }
        .card:nth-child(3) {
            .block__cards {
                h2 {
                    padding-bottom: 76px;
                }
            }
        }
        .card:nth-child(5) {
            .block__cards {
                h2 {
                    padding-bottom: 76px;
                }
            }
        }
        .card:nth-child(6) {
            .block__cards {
                h2 {
                    padding-bottom: 76px;
                }
            }
        }
        .card:nth-child(8) {
            .block__cards {
                h2 {
                    padding-bottom: 76px;
                }
            }
        }
    }
}



