@media screen and(max-width: 1400px) {
    .container {
        width: 1100px;
    }
    .section {
        .section__card {
            .card {
                width: 330px;
                .block__cards {
                    img {
                        width: 100%;
                    }
                    h2 {
                        font-size: 33px;
                    }
                }
            }
        }
    }
}
@media screen and(max-width: 1200px) {
    .container {
        width: 1000px;
    }
    .section .section__card .card .block__cards {
        padding: 15px;
    }
    .section .section__card .card {
        width: 310px;
    }
    .section .section__card .card:nth-child(8) .block__cards h2 {
        padding-bottom: 20px;
    }
    .section h1 {
         margin-left: 0px;
        padding-top: 0px;
        width: auto;
        text-align: center;
    }
    .footer {
        width: 100%;
        height: 559px;
        background: #191919;
        padding: 80px 0px;
    }
    .section {
        margin: 40px 0px 80px 0px;
    }
}
@media screen and(max-width: 1060px) {
    .container {
        width: 740px;
    }
    h1 {
        font-family: "Nunito";
        font-style: normal;
        font-weight: 600;
        font-size: 46px;
        line-height: 135.4%;
        color: #000000;
    }
    .section .section__card .card {
        width: 230px;
    }
    .section .section__card .card .block__cards h2 {
        font-size: 26px;
        line-height: 35px;
    }
    .section .section__card .card:nth-child(3) .block__cards h2 {
        padding-bottom: 20px;
    }
    .section .section__card .card .block__cards {
        padding: 10px;
    }
    .section .section__card .card .block__cards h2 {
        padding-top: 15px;
        padding-bottom: 95px;

    }
    .footer {
        width: 100%;
        height: 519px;
        background: #191919;
        padding: 80px 0px;
    }
    .section .section__card .card .block__cards img {
        width: 100%;
        height: 130px;
    }
    .section .section__card .card {
        height: 325px;
    }
    .section .section__card .card:nth-child(3) .block__cards h2 {
        padding-bottom: 55px;
    }
    .section .section__card .card:nth-child(1) .block__cards h2 {
        padding-bottom: 90px;
    }
    .section .section__card .card:nth-child(2) .block__cards h2 {
        padding-bottom: 55px;
    }
    .section .section__card .card .block__cards h2 {
        padding-top: 15px;
        padding-bottom: 55px;
    }
    .section .section__card .card:nth-child(5) .block__cards h2 {
        padding-bottom: 90px;
    }
    .section .section__card .card:nth-child(6) .block__cards h2 {
        padding-bottom: 90px;
    }
    .section .section__card .card:nth-child(7) .block__cards h2 {
        padding-bottom: 20px;
    }
    .section .section__card .card:nth-child(8) .block__cards h2 {
        padding-bottom: 55px;
    }
}
@media screen and(max-width: 800px) {
    .container {
        width: 540px;
    }
    h1 {
        font-size: 36px;
    }
    .section .section__card {
        padding-top: 20px;
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
    .section .section__card .card {
        width: 260px;
    }
    .section .section__card .card:nth-child(2) .block__cards h2 {
        padding-bottom: 90px;
    }
    .section .section__card .card:nth-child(3) .block__cards h2 {
        padding-bottom: 90px;
    }
    .section .section__card .card:nth-child(7) .block__cards h2 {
        padding-bottom: 55px;
    }
    .section .section__card .card:nth-child(8) .block__cards h2 {
        padding-bottom: 90px;
    }
    .section {
        margin: 30px 0px 60px 0px;
    }
}
@media screen and(max-width: 576px) {
    .container {
        width: 85%;
    }
    .section h1{
        font-size: 25px;
    }
    .section {
        margin: 20px 0px 40px 0px;
    }
    .section .section__card .card {
        width: 100%;
        height: 100%;

    }
    .section .section__card {
        padding-top: 10px;
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
    }
    .section .section__card .card .block__cards {
        padding: 5px;
        margin-bottom: 10px;
        a {
            margin-bottom: 20px;
        }
    }
    .section .section__card .card .block__cards h2 {
        padding-top: 10px;
        padding-bottom: 0px;
        font-size: calc(14px + (8 + 8 * 0.7) * (100vw - 320px) / 576);
        line-height: 25px;
        height: 90px;
    }
    .section .section__card .card .block__cards img {
        width: 100%;
        height: 100%;
    }
    .btn {
        padding: 6px 10px;
        font-size: 14px;
        line-height: 135.4%;
        text-align: center;
        color: #FFFFFF;
    }
}

